<template>
    <div class="page-wrap index-wrap">
        <h2>Toast提示</h2>

        <div class="block">
            <div>
                <a @click.prevent="showToast1">简单传参<i @click.prevent.stop="showCode(1)">code</i></a>
            </div>
            <div>
                <a @click.prevent="showToast2">复杂传参<i @click.prevent.stop="showCode(2)">code</i></a>
            </div>
            <div>
                <a @click.prevent="showToast3">自定义关闭时间<i @click.prevent.stop="showCode(3)">code</i></a>
            </div>
            <div>
                <a @click.prevent="showToast4">倒计时回调<i @click.prevent.stop="showCode(4)">code</i></a>
            </div>
            <div>
                <a @click.prevent="showToast5">全部参数<i @click.prevent.stop="showCode(5)">code</i></a>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'toast',
    data () {
        return {
            codeList: [
                `this.$toast('我是测试的toast文本内容1')`,
				`this.$toast({
					propsData: {
						message: '我是测试的toast文本内容2'
					}
				})`,
                `this.$toast({
					propsData: {
						message: '我是测试的toast文本内容3',
						time: 3000
					}
				})`,
				`this.$toast({
					propsData: {
						message: '我是测试的toast文本内容4',
						time: 3000
					},
					methods: {
						callback () {
							alert('toast 消失了')
						}
					}
				})`
            ]
        }
    },
    created () {
        this.showToast2()
    },
    methods: {
        showToast1 () {
            this.$toast('我是测试的toast文本内容1')
        },
        showToast2 () {
            this.$toast({
                propsData: {
                    message: '我是测试的toast文本内容2',
                    time: 1000000000
                }
            })
        },
        showToast3 () {
            this.$toast({
                propsData: {
                    message: '我是测试的toast文本内容3',
                    time: 3000
                }
            })
        },
        showToast4 () {
            // 注：调用方法除props改用propsData外，其余都与组件内部一致，可使用propsData、data、computed、methods、watch
            this.$toast({
                propsData: {
                    message: '我是测试的toast文本内容4',
                    time: 3000
                },
                methods: {
                    callback () {
                        alert('toast 消失了')
                    }
                }
            })
        },
        showToast5 () {
            // 注：调用方法除props改用propsData外，其余都与组件内部一致，可使用propsData、data、computed、methods、watch
            this.$toast({
                propsData: {
                    message: '我是测试的toast文本内容4',
                    time: 3000
                },
                methods: {
                    callback () {
                        alert('toast 消失了')
                    }
                }
            })
        },
        showCode (index) {
            console.log(this.codeList[index - 1])
        }
    }
}
</script>



