<template>
    <div id="app">
        <router-view />
    </div>
</template>

<script>
export default {
    name: 'App'
}
</script>

<style lang="scss">
#app {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    /*margin-top: 60px;*/

    .page-wrap {
        h2 {
            padding: 20px 0;
            font-size: 32px;
        }
        .block {
            margin-top: 10px;
            div {
                height: 45px;
                line-height: 45px;
                background: #fff;
                padding-left: 20px;
                text-align: left;
                font-size: 15px;
                color: #666;
                position: relative;
                a {
                    display: block;
                    height: 44px;
                    line-height: 44px;
                    border-bottom: 1px solid #eee;
                    color: #666;
                    i {
                        color: #fff;
                        padding: 2px 5px;
                        background-color: #999;
                        border-radius: 5px;
                        font-size: 12px;
                        margin-left: 5px;
                    }
                }
                &:last-child {
                    a {
                        border-bottom: none;
                    }
                }
                &::before {
                    content: "";
                    position: absolute;
                    right: 0.8rem;
                    top: 50%;
                    transform-origin: right center;
                    transform: rotate(45deg);
                    width: 0.53333rem;
                    height: 1px;
                    overflow: hidden;
                    background-color: #999;
                    display: block;
                }
                &::after {
                    content: "";
                    position: absolute;
                    right: 0.8rem;
                    top: 50%;
                    transform-origin: right center;
                    transform: rotate(-45deg);
                    width: 0.53333rem;
                    height: 1px;
                    overflow: hidden;
                    background-color: #999;
                    display: block;
                }
            }
        }
    }
}
</style>



